	padding:0;
	font-family: 'Barlow Semi Condensed', sans-serif;
	background-color: rgb(50,60,143);
	background-image: url('https://www.transparenttextures.com/patterns/diagmonds-light.png');
	height:100%;
}

h1{
	margin:0;
}
p{
	margin:0;
}


.app-container{
	width:100%;
	height:100%;
}


.header-gap{
	height:7em;
}

.card{
	background-color: rgba(52, 167, 238, 0.7);
	width:90%;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin:0 auto;
	margin-top:1em;
	margin-bottom:10em;
	-webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.5);
	        box-shadow: 3px 3px 15px rgba(0,0,0,0.5);
}

form{
	display:-ms-flexbox;
	display:flex;
	width:100%;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
