button{
	font-family: 'Barlow Semi Condensed', sans-serif;
	font-size:1.5em;
	background:white;
	width:80%;
	height:2.5em;
	margin:0.5em 0;
	border-radius:15px;
	border:ridge 3px rgba(0,0,0,0.8);
	-webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	        box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
}

.card-text{
	background:white;
	font-size:1.5em;
	text-align: center;
	width:80%;
	margin: 0.5em auto;
	padding:0.5em;
	border-radius:15px;
	-webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	        box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
}
.text-box{
	border:ridge 3px rgba(0,0,0,0.8);
	-webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	        box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	width:80%;
	font-size:1.5em;
	padding:0.5em;
	height:1.5em;
	margin:0.5em 0;
}

.select-box{
	border:ridge 3px rgba(0,0,0,0.8);
	background:white;
	font-size:1.5em;
	text-align: center;
	width:80%;
	margin: 0.5em auto;
	padding:0.5em;
	border-radius:15px;
	-webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	        box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
	text-align-last: center;
}

/* Slide Container Styles */

.slidecontainer {
    width: 90%;
	margin: 0 auto;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

/* The slider itself */
.slider {
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    width: 100%; /* Full-width */
    height: 50px; /* Specified height */
    background: white;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .3s;
    -webkit-transition: opacity .3s;
    -o-transition: opacity .3s;
    transition: opacity .3s;
	border-radius: 25px;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1;
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 50px;
    height: 50px;
    background: black;
    cursor: pointer; /* Cursor on hover */
	border-radius: 50%;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: black;
    cursor: pointer; /* Cursor on hover */
}
.range{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color:white;
	width:3em;
	font-size:1.5em;
	height:3em;
	border-radius:50%;
	background-color: black;
	margin:0.5em;
	-ms-flex-order:-1;
	    order:-1;
}

.biz{
	background:purple;
}
.indiv{
